import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';

@Component({
  selector: 'app-cookies-policy',
  templateUrl: './cookies-policy.component.html',
  styleUrls: ['./cookies-policy.component.scss']
})
export class CookiesPolicyComponent implements OnInit {

  public breadCrumbs: IBreadcrumb[] = [
    {
      id: 'breadCrumbLinkHome',
      label: 'Voltar para home',
      link: '/'
    },
    {
      id: 'breadCrumbLinkCookiesPolicy',
      label: 'Política de Cookies'
    }
  ];

  constructor(
    private gtm: GtmService,
    private router: Router,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.seoService.createLinkForCanonicalURL();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Política de Cookies');
  }

}
